import { Log } from './log.js';

/** @type {Commerce.Ims.pollImsCountry} */
export function pollCountry({ interval = 200, maxAttempts = 25 } = {}) {
    const log = Log.module('ims');
    return new Promise((resolve) => {
        log.debug('Getting user country');
        let count = 0;

        function poll() {
            // TODO: check if gnav provides better implementation than this
            if (window.adobeIMS) {
                if (window.adobeIMS.isSignedInUser()) {
                    window.adobeIMS.getProfile().then(
                        ({ countryCode }) => {
                            log.debug('Got user country:', countryCode);
                            resolve(countryCode);
                        },
                        (error) => {
                            log.error('Unable to get user country:', error);
                            resolve();
                        }
                    );
                } else {
                    log.debug('User is not signed in');
                    resolve();
                }
                // eslint-disable-next-line no-plusplus
            } else if (++count > maxAttempts) {
                log.debug('Timeout');
                resolve();
            } else {
                setTimeout(poll, interval);
            }
        }

        poll();
    });
}

/**
 * @returns {Commerce.Ims.Client}
 */
export function Ims({}) {
    const imsCountry = pollCountry();
    return { imsCountry };
}
